<template>
    <div class="wrap">
      <div class="point">带 * 为必填项</div>
      <div class="text">
        <div class="xing">*</div>
        <div class="name">公司名称</div>
        <input type="text" placeholder="请输入公司名称" v-model="companyName" value="" :disabled="status">
      </div>
      <div class="text">
        <div class="xing"></div>
        <div class="name">部门名称</div>
        <input type="text" placeholder="请输入部门名称" v-model="departmentName" value="" :disabled="status">
      </div>
      <div class="text">
        <div class="xing">*</div>
        <div class="name">姓名</div>
        <input type="text" placeholder="请输入姓名" v-model="userName" value="" :disabled="status">
      </div>
      <div class="text">
        <div class="xing"></div>
        <div class="name">座机号码</div>
        <input type="text" placeholder="请输入座机号码" v-model="phoneNum" value="" :disabled="status">
      </div>
      <div class="text">
        <div class="xing">*</div>
        <div class="name">手机号码</div>
        <input type="text" placeholder="请输入手机号码" v-model="cellPhoneNum" value="" :disabled="status">
      </div>
      <div class="text">
        <div class="xing">*</div>
        <div class="name">邮箱地址</div>
        <input type="text" placeholder="请输入邮箱地址" v-model="emailAdress" value="" :disabled="status">
      </div>
      <div class="result">
        <div class="text">
          <div class="xing"></div>
          <div class="name">申请时间</div>
          <div class="time">{{time}}</div>
        </div>
        <div class="text" v-if="show">
          <div class="xing"></div>
          <div class="name">申请结果</div>
          <div class="res" v-if="auditState === 0">未通过</div>
          <div class="res" v-else-if="auditState === 1">已授权</div>
          <div class="res" v-else-if="auditState === 2">未审批</div>
          <div class="res" v-else>--</div>
        </div>
      </div>
    </div>
</template>

<script>
  import {formatDate} from "../utils/date";

  export default {
    name: "information",
    props: ['status', 'reason'],
    data() {
      return {
        show: true,
        token: '',
        companyName: '',
        departmentName: '',
        userName: '',
        phoneNum: '',
        cellPhoneNum:  '',
        emailAdress: '',
        time: formatDate(new Date(), 'yyyy-MM-dd'),
        auditState: JSON.parse(sessionStorage.getItem('data')) ? JSON.parse(sessionStorage.getItem('data')).auditState : null
      }
    },
    methods: {
      formCheck() {
        let emailCheck = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/
        if (this.companyName === '') {
          this.$toast('请输入公司名称');
        } else if (this.userName === '') {
          this.$toast('请输入姓名');
        } else if (this.cellPhoneNum === '') {
          this.$toast('请输入手机号码');
        } else if (this.emailAdress === '') {
          this.$toast('请输入邮箱地址');
        } else if (this.cellPhoneNum.length !== 11) {
          this.$toast('请输入正确手机号码格式');
        } else if (!emailCheck.test(this.emailAdress)) {
          this.$toast('请输入正确邮箱地址格式');
        } else {
          return true
        }
      },
      applyAuthority() {
        this.$http.post(backstageIp + userUrl + '/getFlagshipData',
          {
            url: cfaeIp + '/mall//audit/createAudit.json',
            type: 'post',
            cookie: 'token=' + this.token,
            // cookie:'JSESSIONID=763xcJ9LPQLQ1STdXZvYQJGTtcpnvMywTLWyLBg7hpRvlYbN0ywV!1952651716; token=eyJ0eXAiOiJKV1QiLCJhbGciOiJSUzI1NiJ9.eyJ1c2VyT2JqZWN0Ijp7InVzZXJJZCI6IjQ2MDA2IiwidXNlck5hbWUiOiLliJjmmajpmLMiLCJ1c2VyTWFpbCI6bnVsbCwidXNlclJlYWxOYW1lIjoi5YiY5pmo6ZizIiwidXNlck9yZ0lkIjoiMTMzMjEiLCJ1c2VyT3JnTmFtZSI6IuWAuuadg-a1i-.o-XqpKmJVzMiU1PmN9qcND3IoXaUCt1Ih8u0oSlsTtqSXJiBI3I4gA_qwmJ9QE1e8fWswQ4hbFYstmqwIfLfd9CW4AodhkDj7wdsnZi3xKo8qzD_O0hIv3lpe9ZetXoYoiH8HDg5NBTjlKtJto0WDvxmFfc4ts8ibGbsfJ0ujt4; userObject={"attributes":{"EXTEND_USER_ID":"093b7d45d11c478fa7918cfbeacf71f8","TENANT_ID":"default","lastIp":"10.2.37.53","lastLogin":"2019-03-14 10:36:17.578","menutype":null,"mobile":null,"orgCode":"1141","orglist":"13321","parentOrgIds":"","pctid":null,"roleCode":"org_admin","roleList":"1062"},"sessionId":null,"uniqueId":null,"userId":"46006","userMail":null,"userName":"åæ¨é³","userOrgId":"13321","userOrgName":"åºææµè¯æèµäºº","userRealName":null,"userRemoteIP":"10.2.37.53"}',
            formData: JSON.stringify({
              applyUnit: this.companyName,
              applyDepartment: this.departmentName,
              applyName: this.userName,
              applyTel: this.phoneNum,
              applyPhone: this.cellPhoneNum,
              applyEmail: this.emailAdress,
              sex: this.reason
            })
          }
        ).then(res => {
          let data = JSON.parse(res.data.data);
          console.log(data);
          if(data.success){
            this.$router.push('/mall/gtja/inReview')
          }else{
            this.$toast(data.msg);
          }

        }).catch(err => {
          this.$toast('当前无网络连接或网络不稳定，请稍后重试');
        });
      }

    },
    created() {
      this.token = sessionStorage.getItem('token');
      if (this.$route.fullPath === '/mall/gtja/informationDetail') {
        let data = JSON.parse(sessionStorage.getItem('data'));

        this.companyName = data.applyUnit || '--';
        this.departmentName = data.applyDepartment || '--';
        this.userName = data.applyName || '--';
        this.phoneNum = data.applyTel || '--';
        this.cellPhoneNum = data.applyPhone || '--';
        this.emailAdress = data.applyEmail || '--';
        this.time = data.applyTime || '--'
      }
      if (this.$route.fullPath === '/mall/gtja/applyInformation') {
        this.show = false
      }
    },
    mounted() {

    }
  };
</script>

<style scoped>
  .name {
    width: 60px;
    padding: 0 17px 0 7px;
    font-size:15px;
    font-family:PingFangSC-Medium;
    font-weight:500;
    color:rgba(32,32,32,1);
    line-height:21px;
  }
  .point {
    padding: 6px 0 6px 15px;
    font-size:10px;
    font-family:PingFangSC-Regular;
    font-weight:400;
    color:rgba(124,124,125,1);
    line-height:14px;
  }
  .text {
    display: flex;
    align-items: center;
    padding: 13px 0 13px 9px;
    background: #fff;
    position: relative;
  }
  .text input {
    outline: none;
    border: 0;
    width: 66%;
    background: #fff;
  }
  .text .xing {
    width:8px;
    height: 21px;
    font-size:15px;
    font-family:PingFangSC-Medium;
    font-weight:500;
    color:rgba(228,43,66,1);
    line-height:21px
  }
  .text::before {
    content: '';
    height: 1px;
    position: absolute;
    bottom: 0;
    right: 0;
    left: 15px;
    background:rgba(238,238,238,1);
  }
  .text:nth-last-of-type(1)::before {
    content: '';
    height: 0;
    position: absolute;
    bottom: 0;
    right: 0;
    left: 15px;
    background:rgba(238,238,238,1);
  }
  .text:nth-of-type(7)::before {
    content: '';
    height: 0;
    position: absolute;
    bottom: 0;
    right: 0;
    left: 15px;
    background:rgba(238,238,238,1);
  }
  input {
    font-size:13px;
    font-family:PingFangSC-Regular;
    font-weight:400;
    color:rgba(32,32,32,1);
    line-height:19px;
  }
  input::placeholder {
    font-size:13px;
    font-family:PingFangSC-Regular;
    font-weight:400;
    color:rgba(167,167,167,1);
    line-height:19px;
  }
  .result {
    margin-top: 11px;
    font-size:13px;
    font-family:PingFangSC-Regular;
    font-weight:400;
    color:rgba(32,32,32,1);
    line-height:19px;
  }
</style>



