<!--申请信息-->
<template>
    <div class="wrap">
      <van-nav-bar
        title="申请信息"
        left-text="返回"
        left-arrow
        fixed
        @click-left="onClickLeft"
      />
      <div class="mainContent">
        <information :status="status" :reason="radio" ref="mychild"></information>
        <div class="btn">
          <div class="throw" @click="apply">
            申请
          </div>
        </div>
      </div>
      <van-dialog
        v-model="show"
        show-cancel-button
        title="申请理由(选填)"
        confirmButtonText="申请开通"
        :before-close="beforeClose"
      >
        <p class="txt">方便管理员尽快通过您的申请</p>
        <van-radio-group v-model="radio" shape="square" checked-color="#DDA944" @change="choose">
          <van-radio name="1">我是贵公司客户，请协助开通我的产品权限</van-radio>
          <van-radio name="2">我的产品权限已过期，重新申请</van-radio>
          <van-radio name="3">其他</van-radio>
        </van-radio-group>
      </van-dialog>
    </div>
</template>

<script>
  import information from '../../../components/information'
    export default {
      name: "applyInformation",
      components: {
        information
      },
      data() {
          return {
            show: false,
            radio: '1',
            status: false
          }
      },
      methods: {
        onClickLeft(){
          this.$router.back();
        },
        // 点击申请
        apply () {
          if (this.$refs.mychild.formCheck()) {
            this.show = true
          }
        },
        // 选择申请理由
        choose (e) {
          console.log(e)
        },
        // 申请开通
        beforeClose(action, done) {
          if (action === 'confirm') {
            this.$refs.mychild.applyAuthority()
          } else {
            this.radio = '1';
            done();
          }
        }
      }
    }
</script>

<style scoped>
  .wrap {
    background:rgba(247,248,250,1);
  }
  .mainContent {
    margin-top: 46px;
    min-height: calc(100vh - 46px);
  }
.btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 42px 41.5px 88px 41.5px;
}
  .throw {
    width:75px;
    height:35px;
    text-align: center;
    background:rgba(221,169,68,1);
    box-shadow:0px 6px 14px 0px rgba(221,169,68,0.5);
    border-radius: 28px;
    font-size:17px;
    font-family:PingFangSC-Medium;
    font-weight:500;
    color:rgba(255,255,255,1);
    line-height:35px;
  }
  .txt {
    font-family:PingFangSC-Medium;
    text-align: center;
    font-size: 15px;
    margin: 10px 0 20px;
  }
  >>>.van-dialog__header {
    font-family:PingFangSC-Medium;
    font-size: 20px;
    font-weight: 500;
  }
  >>>.van-radio__label {
    font-size: 13px;
  }
  >>>.van-radio__icon {
    margin: 0 0 0 20px;
  }
  >>>.van-radio {
    margin-bottom: 10px;
  }
</style>



